<style>
.HistoryImg {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  box-sizing: border-box;
  border: none;
}
</style>
<template>
  <div class="special" :style="{backgroundImage:'url('+eleList[0].specialImg+')'}">
    <div class="special_box">
      <h2>{{msg}}</h2>
      <div class="right_ml">
        <div class="btn_sc change">
          <span v-if="!show">
            <!-- <i class="iconfont" v-if="!show">&#xe658    </i> -->
            <span class="jlt2" v-if="!show"></span>
          </span>
          <p v-if="!show" class="wz">已经</p><span class="jlt1" v-if="show"></span><p class="wz">收藏</p>
        </div>
        <div class="btn_ff" v-show="!isAuth">
          <div class="wz">
            <span class="jlt3"></span>
            <p>付费观看</p>
          </div>
        </div>
      </div>
      <div class="right_js">
        <div class="contentj">
          <h4>{{text.assetName}}</h4>
          {{text.description}}
        </div>
      </div>
    </div>
    <div class="special_list">
      <ul>
        <li v-for="(item,index) in itemList" :key="item.id" :id="('Img_'+index)">
          <lazy-image :src='item.elementImg' :placeholder='placeholder' :initScore='initScore' :score="item.score" :title="item.elementName"
            :imgClass='HistoryImg' />
        </li>
      </ul>
    </div>
    <div class="gunLun" v-if="showScroll">
      <div class="btn_num">{{num+1}}/{{itemList.length}}</div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    <order v-if="isOrder" v-on:controllMeg="componentmMsg" :orders="orderList" :assetInfo="assetInfo" ></order>
  </div>
</template>
<style scoped="Special">
.changedImg {
  border: 3px solid #ffffff;
  border-radius: 15px;
}

.right_ml .change {
  background: #0098bc;
  border: 2px solid white;
}

.special {
  width: 1280px;
  height: 720px;
  border-top: 1px solid black;
  background-size: 100% 100%;
}

.special_box {
  margin-left: 75px;
  margin-top: 43px;
}

.special_box:after {
  clear: both;
  content: "";
  display: block;
}

.special .right_ml {
  float: left;
}

h2 {
  width: 1200px;
  color: white;
  font-size: 49px;
}

.btn_sc {
  float: left;
  box-sizing: border-box;
  width: 148px;
  height: 50px;
  border-radius: 16px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  margin-top: 28px;
  margin-right: 15px;
}

.btn_ff {
  float: left;
  box-sizing: border-box;
  width: 148px;
  height: 50px;
  border-radius: 16px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  margin-top: 28px;
}
.right_js h4{
  position: relative;
  margin-bottom:10px;
}
.right_js {
  position: relative;
  float: right;
  margin-right: 110px;
  font-family: PingFangSC-Regular;
  color: #ffffff;
  width: 504px;
  height: 240px;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.75);
  font-size: 20px;
}

.right_js .contentj {
  line-height: 30px;
  font-family: PingFangSC-Regular;
  font-size: 20px;
  color: #ffffff;
  text-align: left;
  margin-top: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  margin-left: 39px;
  width: 442px;
}

.special_list {
  margin-left: 82px;
  width: 1120px;
  overflow: hidden;
  margin-top: 38px;
}

.special ul {
  width: 100000%;
}

.special ul:after {
  clear: both;
  content: "";
  display: block;
}

.special ul li {
  width: 172px;
  height: 230px;
  float: left;
  margin-right: 16px;
  box-sizing: border-box;
}

.gunLun {
  width: 1133px;
  height: 13px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  margin-top: 45px;
  margin-left: 78px;
  position: relative;
}

.gunLun .btn_num {
  width: 200px;
  height: 27px;
  background: white;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  left: 0px;
  top: -7px;
  color: black;
}

.jlt1 {
  width: 35px;
  height: 35px;
  margin-left: 34px;
  padding: 8px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -105px;
}

.jlt2 {
  width: 35px;
  height: 35px;
  margin-left: 15px;
  padding: 8px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -105px -105px;
}

.jlt3 {
  width: 35px;
  height: 35px;
  margin-left: 15px;
  padding: 8px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -175px -175px;
}

p {
  font-size: 19px;
  display: inline-block;
  line-height: 50px;
}
</style>

<script>
import { mapGetters } from "vuex";
import { HTTPUtil as api } from "../../fetch/api.js";
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import { Authentication as auth } from "../../js/auth.js";
import order from "../../tool/order/order.vue";
import Toast from "../../tool/toast/toast.js";
import { xha as x } from "../../js/xha.js";
export default {
  props: ["jsonUrl", "bp"],
  name: "Special",
  data() {
    return {
      show: true,
      op: 0,
      ops: true,
      isShow: true,
      num: 0,
      item: 18,
      HistoryImg: "HistoryImg",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      //        jsonUrl: "http://183.131.15.28/?s=19|7&p=yhSpecialDetail&k=1&v=2&specialId=202688&c=7",
      //        jsonUrl: "http://183.131.15.28/?s=19|7&p=yhSpecialDetail&k=1&v=2&specialId=202737&c=7",
      msg: "夜幕下的霓虹，你会爱它多久",
      text: {description:""},
      description: "",
      eleList: [],
      itemList: [],
      textUrl: "",
      tra: "",
      traceTypes: "",
      traceType: "",
      itTime: "",
      pageType: 8,
      specialType: 2,
      isOrder: false,
      orderList: null, // 可订购列表
      assetInfo: null,
      isAuth: true, //  鉴权标识 2018/10/26 屏蔽专题入口
      authStartTime: 0, // 鉴权开始时间
      authEndTime: 0, // 鉴权结束时间
      columnId: 0,
      pageId: 0,
      pid: 0, // 可订购产品包ID
      sid: 0, // 可订购产品包服务ID
      sname: "", // 可订购产品名称
      detailData: null,
      authCode:0,// 鉴权code
      showScroll:false,
    };
  },
  created() {
    window.topicAuth = this.topicAuth;
    window.topicToken = this.topicToken;
    this.init();
  },
  mounted() {
    this.$nextTick(function(){
      setTimeout(() => {
        $("#btnss" + this.opTime).addClass("changes");
      }, 100);
    })
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  watch: {
    eleList() {
      // 监听数据变化并作出处理
      this.$nextTick(function() {
        this.textUrl = this.itemList[this.num].jsonUrl;
        this.inits(this.textUrl);
        this.isShow = true;
        this.load();
      });
    }
  },
  methods: {
    getAssentsPPV() {
      // CMS 查询绑定关系
      this.pageId = this.itemList[0].elementId;
      let assetName = this.itemList[0].elementName;
      let spCode = this.itemList[0].providerId;
      this.assetInfo = { contentCode: this.pageId, contentName: assetName,assetSp:spCode};
      api
        .getProducts(this.pageId, this.columnId)
        .then(jsonData => {
          console.log(JSON.stringify(jsonData));
          this.isOnceReq = false; // 有鉴权结果就不需要再鉴权了
          if (jsonData.code == 200) {
            if (jsonData.data.result.length > 0) {
              console.log("收费资产：资产ID:" + this.pageId);
              const pob = jsonData.data.result[0].pgList[0];
              console.log(pob);
              this.pid = pob.productCode;
              this.sid = pob.serviceCode;
              this.sname = pob.serviceName;
              console.log("pob.productCode=======" + pob.productCode);
              // let params = {};
              //     params.url = "/api/Auth";
              //     params.type = "post";
              //     params.data = JSON.parse(auth.AuthParams(this.pid,this.sid,this.pageId));
              //     api.ajax(params).then((jsonData)=>{
              //         this.topicAuth(jsonData);
              // })
              this.authStartTime = new Date().getTime();
              yh.HttpPost(
                auth.Auth,
                auth.AuthParams(this.pid, this.sid, this.pageId),
                "topicAuth"
              ); // 产品包鉴权请求
            } else {
              console.log("免费资产直接播");
              this.isAuth = true;
            }
          }
        })
        .catch(() => {
          Toast({ message: "CMS查询产品包信息异常", position: "bottom" });
          // this.isAuth = false;
          this.isAuth = true; // 2018/10/26 屏蔽专题收费入口

        });
    },
    topicAuth(jsonData) {
      // BMS 专题
      this.authEndTime = new Date().getTime() - this.authStartTime;
      console.log("BMS响应时间:" + this.authEndTime);
      console.log("请求BMS 鉴权返回:" + JSON.stringify(jsonData));
      if (jsonData.code == 200) {
        if (jsonData.result) {
          // 存在可订购列表
          this.authCode = jsonData.result.resultCode;
          if (jsonData.result.resultCode == "0000") {
            console.log("已订购");
            this.isAuth = true;
            // yh.updatePreViwe(false); // 鉴权成功通知播放器无需试看
          }else if(jsonData.result.resultCode == "1001"||jsonData.result.resultCode == "1002"||jsonData.result.resultCode =="1005" ){
              this.orderList = null;
              // this.isAuth = false;
              this.isAuth = true; // 2018/10/26 屏蔽专题收费入口
          } else {
            this.orderList = jsonData.result.products;
            // this.isAuth = false;
            this.isAuth = true; // 2018/10/26 屏蔽专题收费入口
            console.log("未订购");
          }
        }
      } else if (jsonData.code === 1004 || "1004" === jsonData.code) {
        // 用户token失效，续租
        console.log("token令牌失效重新续租");
        // this.isAuth = false;
        this.isAuth = true; // 2018/10/26 屏蔽专题收费入口
        this.restUserToken();
      } else {
        this.isAuth = true; // 鉴权失败
        Toast({ message: "用户鉴权失败,请退出重试", position: "bottom" });
        yh.updatePreViwe(false); // 鉴权超时通知播放器无需试看
      }
    },
    restUserToken() {
      // 续租token
      yh.HttpPost(auth.Login, auth.LoginParams(), "topicToken"); // 终端请求
    },
    topicToken() {
      // BMS 续租结果处理
      if (jsonData.code == 200) {
        if (jsonData.result.resultCode == "0000") {
          try {
            yh.setAuthInfo(jsonData.result);
            localStorage.setItem("AAA", "true");
            localStorage.setItem("AuthResult", JSON.stringify(jsonData.result));
            this.$store.commit("GET_AUTH", "true");
            yh.HttpPost(
              auth.Login,
              auth.AuthParams(this.pid, this.sid, this.pageId),
              "topicAuth"
            ); // 续租成功继续鉴权
          } catch (error) {
            console.log("鉴权成功，处理参数转换异常");
          }
        } else {
          localStorage.setItem("AAA", "false");
          this.$store.commit("GET_AUTH", "false");
          Toast("续租token失败,用户账号异常");
        }
      } else {
        Toast("续租token失败,请退出详情页重试");
      }
    },
    componentmMsg(bl, name, pos, url) {
      // 处理支付组件之间的消息
      console.log(bl, name, pos, url);
      if (name == "order" || name == "orderBack") {
        if (pos == "orderCancle") {
          this.isOrder = false;
          this.isShow = true;
          if (url === "true" || url === true) {
            Toast("支付成功~");
          } else {
            Toast("支付失败~");
          }
        } else if (pos == "orderBack") {
          this.isOrder = false;
          this.isShow = true;
        }
      }
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        this.KeyEnter();
      }
    },
    load() {
      try {
        let pageId = this.eleList[0].specialId;
        c.setTrackPath(pageId, this.pageType);
        this.itTime = new Date().getTime();

        //          获取路径ID
        let traceId = c.getTrackPath();
        //          获取路径
        let traceType = c.getTrackPathType();
        //          获取父Id
        let catId = c.getParentPageId();
        //          获取父类型
        let catCode = c.getParentPageType();
        let oldTime =
          performance.timing.domContentLoadedEventEnd -
          performance.timing.navigationStart;
        //        bp.loadPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,special_type,oldTime)
        bi.loadPage(
          traceId,
          traceType,
          this.pageType,
          pageId,
          catCode,
          catId,
          oldTime
        );
        console.log(
          traceId,
          traceType,
          this.pageType,
          pageId,
          catCode,
          catId,
          oldTime
        );

        let PagePath = c.getTrackPathType().replace(/\|/gi, "_");
        let SrcPage = PagePath.split("_");
        let SrcPagePath = SrcPage.slice(0, SrcPage.length - 1).join("_");
      } catch (error) {
        console.log(error);
      }
    },
    closPages() {
      // 离开页面（页面停留时间）
      let time = new Date().valueOf();
      let times = time - this.oldTimes;
      this.pageId = this.text.assetId;
      // 获取路径ID
      let traceId = c.getTrackPath();
      // 获取路径
      let traceType = c.getTrackPathType();
      let special_type = this.specialType;
      bi.closPage(
        traceId,
        traceType,
        this.pageType,
        31,
        this.pageId,
        special_type,
        times
      );
    },
    findindex() {
      let storage = JSON.parse(localStorage.getItem("PersonInfo"));
      for (let i = 0; i < storage.length; i++) {
        if (storage[i].specialId == this.eleList[0].specialId) {
          return i;
        }
      }
    },
    //c查看本地缓存是否存在
    queryFavorites() {
      let storage = JSON.parse(localStorage.getItem("PersonInfo"));
      if (storage) {
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].specialId == this.eleList[0].specialId) {
            return true;
          }
        }
      }
    },
    getDetail(curl) {
      let that = this;
      // curl = "http://183.131.15.11:8490/?s=11|1&p=yhSpecialDetail&k=1&v=1&specialId=202870&c=1&returnType=jsonp";
      api
        .get(curl)
        .then(
          data => {
            let catList = data.data;
            that.eleList.splice(0, that.eleList.length); //清空数组
            that.eleList.push(catList);
            that.itemList = this.eleList[0].elementList;
            that.msg = this.eleList[0].specialName;
            if (that.queryFavorites()) {
              this.show = false;
            } else {
              this.show = true;
            }
            that.getAssentsPPV();
            this.textUrl = this.itemList[this.num].jsonUrl;
            this.inits(this.textUrl);
          },
          response => {
            console.log("获取详情页数据失败");
          }
        )
        .then(() => {
          this.load();
        });
    },
    inits(Url) {
      let curl;
      if (Url) {
        this.childrenUrl = Url;
        curl = Url + this.behindParams;
        console.log(curl);
      } else {
        this.childrenUrl = curl;
        curl += this.behindParams;
        console.log(curl);
      }
      this.getDetails(curl);
    },
    getDetails(curl) {
      let that = this;
      api.get(curl).then(
        data => {
          that.text = data.data;
        },
        response => {
          console.log("获取详情页数据失败");
        }
      );
    },
    init() {
      let curl;
      if (this.$route.query.jsonUrl) {
        curl = this.$route.query.jsonUrl + this.behindParams;
        this.childrenUrl = this.$route.query.jsonUrl;
      } else {
        if (this.jsonUrl) {
          this.childrenUrl = this.jsonUrl;
          curl = this.jsonUrl + this.behindParams;
          localStorage.setItem("hots", this.jsonUrl);
        } else {
          console.log(this.jsonUrl);
          curl = localStorage.getItem("hots") || "";
          this.childrenUrl = curl;
          curl += this.behindParams;
        }
      }
      this.getDetail(curl);
    },
    back() {
      this.$router.go(-1);
    },
    addCss() {
      $("#Img_" + this.num).addClass("changedImg");
    },
    removeCss() {
      $("#Img_" + this.num).removeClass("changedImg");
    },
    down() {
      if (this.op == 0) {
        $(".btn_sc").removeClass("change");
        $(".btn_ff").removeClass("change");
        this.ops = true;
        this.op++;
        this.addCss();
      }
    },
    up() {
      if (this.op == 1) {
        $(".btn_sc").addClass("change");
        this.op--;
        this.removeCss();
      }
    },
    left() {
      if (this.op == 0) {
        $(".btn_ff").removeClass("change");
        $(".btn_sc").addClass("change");
        this.ops = true;
      } else {
        if (this.num < 1) return;
        let left = this.cEleOffsetLeft("#Img_" + this.num);
        if (left < 100) {// 解决翻页滚动到第二个不能继续滚动
          $(".special_list ul").css({
            transition: "all 0.1s",
            transform: "translateX(-" + 188 * (this.num - 1) + "px)"
          });
        }
        this.removeCss();
        this.num--;
        this.leftMove();
        this.addCss();
        this.textUrl = this.itemList[this.num].jsonUrl;
        this.inits(this.textUrl);
      }
    },
    right() {
      if (this.op == 0) {
        if (!this.isAuth) {
          $(".btn_sc").removeClass("change");
          $(".btn_ff").addClass("change");
          this.ops = false;
        }
      } else {
        let left = this.cEleOffsetLeft("#Img_" + this.num);
        if (this.num > this.itemList.length - 2) return;
        if (left > 900) {
          $(".special_list ul").css({
            transition: "all 0.1s",
            transform: "translateX(-" + 188 * (this.num - 4) + "px)"
          });
        }
        this.removeCss();
        this.num++;
        this.rightMove();
        this.addCss();
        this.textUrl = this.itemList[this.num].jsonUrl;
        this.inits(this.textUrl);
      }
    },
    pageLode(asssetId,asssetName) {
      // 星红桉埋点
      let SrcPagePath = this.category[this.navpos].catName;
      let PageName = asssetName;
      let AsssetID = asssetId;
      let Id = localStorage.getItem("position").split('_')[0];
      let TYPE = "";
      x.pageLoad("专题",SrcPagePath, PageName, AsssetID, Id, TYPE);
    },
    Collect(Status){
      // 星红桉埋点
      let cId = this.eleList[0].specialId; // 媒资ID
      let cName = this.eleList[0].specialName; // 媒资名字
      let cStatus = Status ; // 媒资收藏状态
      x.collect(cId, cName, cStatus);
    },
    KeyEnter() {
      //op=0上方收藏
      if (this.op == 0) {
        if (this.ops) {
          if (this.show) {
            this.Collect(1)
            this.show = false;
            //H5本地缓存
            let Tvsc = {
              specialId: this.eleList[0].specialId,
              assetName: this.eleList[0].specialName,
              assetType: "专题",
              assetImg: this.eleList[0].coverimage,
              episodes: "",
              broadcast: "",
              score: "",
              coverimage: this.eleList[0].coverimage,
              specialName: this.eleList[0].specialName,
              layout: "",
              layoutUrl: this.jsonUrl
            };
            let storage = localStorage.getItem("PersonInfo");
            if (storage) {
              storage = JSON.parse(storage);
            } else {
              storage = [];
            }
            storage.push(Tvsc);
            localStorage.setItem("PersonInfo", JSON.stringify(storage));
            this.Coll();
          } else {
            this.Collect(2)
            this.show = true;
            //H5删除收藏
            let storage = JSON.parse(localStorage.getItem("PersonInfo"));
            let json = [];
            for (let i = 0; i < storage.length; i++) {
              if (i != this.findindex()) {
                json.push(storage[i]);
              }
            }
            localStorage.setItem("PersonInfo", JSON.stringify(json));
            this.NoColl();
          }
        } else {
          if (!this.isAuth) {
            if (this.orderList == null) {
              if(this.authCode==0){
                Toast({ message: "正在查询中请稍后~", position: "bottom" });
              }else if(this.authCode=="1001"||this.authCode=="1002" || this.authCode=="1005"){
                Toast({ message: "账户不存在或非法，请联系客服", position: "bottom" });
              }else{
                Toast({ message: "鉴权异常，请退出重试。", position: "bottom" });
              }
              
            } else {
              this.isShow = false;
              this.isOrder = true;
            }
          } else {
            Toast({ message: "您已经订购过啦~", position: "bottom" });
          }
        }
      } else {
        //下方点击跳转
        this.pageLode(this.text.assetId,this.text.assetName);
        let url = this.itemList[this.num].jsonUrl;
        //          this.categoryCilck();
        let obj = { special_type: 2 };

        c.routerSkip(url, "1", obj, this.$router);
        window.localStorage.setItem('EnterType','2');
      }
    },
    //计算物体到左边的距离
    cEleOffsetLeft(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.left;
      return realHeight;
    },
    //收藏
    Coll() {
      //        asset_id, collect_type, type
      try {
        let pageId = this.eleList[0].specialId;
        //        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(pageId, 0, 1);
      } catch (error) {
        console.log(error);
      }
    },
    //取消收藏
    NoColl() {
      try {
        let pageId = this.eleList[0].specialId;
        //        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(pageId, 0, 2);
      } catch (error) {
        console.log(error);
      }
    },
    rightMove() {
      let number = this.eleList[0].elementList.length;
      // let ly = Math.ceil(1133 / number - 10);
      let ly = (1133-200)/number;
      $(".btn_num").css({
        transition: "all 0.4s",
        transform: "translateX(" + ly*(this.num+1) + "px)"
      });
      console.warn('移动距离:'+ly*(this.num+1)+'=='+(this.num+1))
    },
    leftMove() {
      let number = this.eleList[0].elementList.length;
      // let ly = Math.ceil(1133 / number - 10);
      let ly = (1133-200)/number;
      if(this.num == 0){
        $(".btn_num").css({
          transition: "all 0.4s",
          transform: "translateX(" + ly*(this.num) + "px)"
        });
      }else{
        $(".btn_num").css({
          transition: "all 0.4s",
          transform: "translateX(" + ly*(this.num+1) + "px)"
        });
      }
      console.warn('移动距离:'+ly*(this.num+1)+'=='+(this.num+1))
    }
  },
  computed: {
    ...mapGetters([
      "rootPath",
      "catList",
      "category",
      "behindParams",
      "isHTTPS",
      "httpRequestTime",
      "httpResponseTime",
       "navpos"
    ])
  },
  watch: {
    special() {
      this.$nextTick(function() {
        this.isEnd = false;
        this.pageId = this.special.assetId;
        this.load();
      });
    },text(){
      
        let jianjie = this.text.description;
        let reslut =  this.text.description;
        if(jianjie.indexOf('简介')>=0){
          reslut =  jianjie.substring(3);
        }else if(jianjie.indexOf('简介:')>=0){
          reslut = jianjie.substring(3);
        }else if(jianjie.indexOf('简介：')>=0){
          reslut = jianjie.substring(3);
        }
         this.text.description = '简介：'+ reslut;
    },
    itemList(){
      if(this.itemList.length>0){
        this.showScroll = true;
      }else{
        this.showScroll = false;
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    this.removeCss();
    this.op = 0;
    try {
      let pageId = this.eleList[0].specialId;
      //          获取路径ID
      let traceId = c.getTrackPath();
      //          获取路径
      let traceType = c.getTrackPathType();

      let overTime = 0;
      overTime = new Date().getTime() - this.itTime;
      // this.closPages();

      //        traceId, traceType, pageType, pageId, special_type, oldTime
      bi.closPage(traceId, traceType, this.pageType, pageId, 2, overTime);
    } catch (error) {
      console.log(error);
    } finally {
      next();
    }
  },
  beforeRouteEnter (to, from, next) {
    window.sessionStorage.removeItem('srcPagePath');
    window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    next();
  },
  components: { order, Toast }
};
</script>



