<style>
.HistoryImg {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  box-sizing: border-box;
  border: none;
}
.imgsss {
  width: 100%;
  height: 150px;
  border: 3px solid white;
  border-radius: 50%;
}
</style>
<template>
  <div class="special" :style="{backgroundImage:'url('+eleList.backgroundImg+')'}">
    <div class="special_Img" v-if="!(eleList.backgroundImg)">
      <lazy-image
        :src='eleList.headImg'
        :placeholder='placeholder'
        :initScore = 'initScore'
        :imgClass='imgsss'
      />
      <p class="actorName">{{eleList.actorName.substring(0,1)}}</p>
    </div>
    <div class="special_box">
      <h2>{{eleList.actorName}}</h2>
      <div class="right_js">
        {{eleList.introduce}}
      </div>
      <div class="right_ml" style="box-sizing: border-box">
        <div class="btn_sc">
              <span v-if="!show" class="jlt2"></span>
             <span v-if="!show">已经</span><span v-if="show" class="jlt1"></span>收藏
        </div>
        <!-- 明星无介绍信息需要隐藏介绍按钮 -->
        <!-- <div class="btn_js"><span class="jlt3"></span>介 绍</div> -->
      </div>
    </div>
    <div class="special_list">
      <ul>
        <li  v-for="(item,index) in itemList"  :key="item.id" :id="('Img_'+index)">
          <lazy-image
            :src='item.assetImg'
            :placeholder='placeholder'
            :initScore = 'initScore'
            :score = "item.score"
            :title = "item.elementName"
            :imgClass='HistoryImg'
            :oragin="item.providerId"
          />
        </li>
      </ul>
    </div>
    <div class="gunLun" v-if="itemList.length>5">
      <div class="btn_num">{{num+1}}/{{itemList.length}}</div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    <description ref="description" :isAlert="alertMsg" v-on:press="kDo"></description>
  </div>
</template>
<style scoped="actor">
.actorName {
  position: absolute;
  font-size: 55px;
  color: #ffffff;
  width: 100px;
  height: 100px;
  top: 110px;
  left: 132px;
}
.special_Img {
  width: 150px;
  height: 280px;
  float: left;
  margin-right: 33px;
}
special_box {
  float: left;
}
.changedImg {
  border: 3px solid #ffffff;
  border-radius: 15px;
}
.right_ml .change {
  background: #0098bc;
  border: 2px solid white;
  margin-top: -2px;
  margin-left: -2px;
}
.right_ml {
  margin-top: 34px;
  height: 60px;
}
.special {
  width: 1198px;
  height: 651px;
  padding-left: 82px;
  padding-top: 69px;
  background-size: 100% 100%;
}
.special .right_ml:after {
  clear: both;
  content: "";
  display: block;
}
h2 {
  width: 1000px;
  color: white;
  font-size: 35px;
}
.btn_sc {
  float: left;
  width: 148px;
  height: 50px;
  border-radius: 15px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
  line-height: 50px;
  font-size: 19px;
  margin-right: 15px;
}
.btn_js {
  float: left;
  width: 148px;
  height: 50px;
  border-radius: 15px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
  line-height: 50px;
  font-size: 20px;
}
.right_js {
  font-family: PingFangSC-Regular;
  font-size: 20px;
  color: #ffffff;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-right: 110px;
  margin-top: 18px;
  width: 401px;
}
.special_list {
  width: 1120px;
  overflow: hidden;
  margin-top: 93px;
}
.special ul {
  width: 100000%;
}
.special ul:after {
  clear: both;
  content: "";
  display: block;
}
.special ul li {
  width: 172px;
  height: 230px;
  float: left;
  margin-right: 16px;
  box-sizing: border-box;
}
.gunLun {
  width: 1133px;
  height: 13px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  margin-top: 45px;
  position: relative;
}
.gunLun .btn_num {
  width: 200px;
  height: 27px;
  background: white;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  left: 0px;
  top: -7px;
  color: black;
}
.jlt1 {
  width: 35px;
  height: 35px;
  padding: 5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -105px;
}
.jlt2 {
  width: 35px;
  height: 35px;
  padding: 5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -105px -105px;
}

.jlt3 {
  width: 35px;
  height: 35px;
  padding: 5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -315px;
}
</style>

<script>
import { mapGetters } from "vuex";
import { HTTPUtil as api } from "../../fetch/api.js";
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import { xha } from "../../js/xha.js";
import description from "../detail/Category_Movie_description.vue";
export default {
  props: ["jsonUrl", "bp"],
  name: "actor",
  data() {
    return {
      show: true,
      op: 1,
      js: false,
      isShow: true,
      alertMsg: {},
      num: 0,
      item: 18,
      imgsss: "imgsss",
      oldTime: 0,
      HistoryImg: "HistoryImg",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      //        jsonUrl: "http://183.131.15.28/?s=19|7&p=yhActorDetail&k=1&v=2&actorId=196",
      msg: "夜幕下的霓虹，你会爱它多久",
      text: [],
      eleList: {},
      itemList: [],
      textUrl: ""
    };
  },
  created() {
    this.init();
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  watch: {
    eleList: function(value) {
      // 监听数据变化并作出处理
      this.$nextTick(function() {
        this.isShow = true;
        if (this.queryFavorite()) {
          this.show = false;
        } else {
          this.show = true;
        }
      });
    }
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        this.KeyEnter();
      }
    },
    Collect(Status){
      // 星红桉埋点
      let cId = this.eleList.actorId; // 媒资ID
      let cName = this.eleList.actorName; // 媒资名字
      let cStatus = Status ; // 媒资收藏状态
      xha.collect(cId, cName, cStatus);
    },
    pageLoad() {
        let SrcPagePath = window.sessionStorage.getItem('srcPagePath');
        let PageName = '演员详情页';
        let AsssetID = this.eleList.actorId;
        let Id = '';
        let TYPE = '';
        xha.pageLoad("演员专题",SrcPagePath,PageName,AsssetID,Id,TYPE);
    },
    load() {
      this.oldTime = new Date().getTime();
      try {
        let pageId = this.eleList.actorId;
        this.pageId = pageId;
        c.setTrackPath(this.pageId, 4);
        //          获取路径ID
        let traceId = c.getTrackPath();
        //          获取路径
        let traceType = c.getTrackPathType();
        //          获取父Id
        let catId = c.getParentPageId();
        //          获取父类型
        let catCode = c.getParentPageType();
        let oldTime =
          performance.timing.domContentLoadedEventEnd -
          performance.timing.navigationStart;
        //        bp.loadPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,special_type,oldTime)
        bi.loadPage(traceId, traceType, 4, pageId, catId, catCode, oldTime);
      } catch (error) {
        console.log(error);
      }
    },
    findindex() {
      let storage = JSON.parse(localStorage.getItem("PersonMovies"));
      let findeID = 0;
      for (let i = 0; i < storage.length; i++) {
        if (storage[i].assetId == this.eleList.actorId) {
          findeID = i;
          break;
        }
      }
      return findeID;
    },
    //c查看本地缓存是否存在
    queryFavorite() {
      let storage = JSON.parse(localStorage.getItem("PersonMovies"));
      if (storage) {
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].assetId == this.eleList.actorId) {
            return true;
          }
        }
      } else {
        return false;
      }
    },
    getDetail(curl) {
      let that = this;
      api.get(curl).then(
        data => {
          let catList = data.data;
          that.eleList = catList;
          this.load();
          that.itemList = this.eleList.assetList;
          that.msg = this.eleList.actorName;
          if (that.queryFavorite()) {
            this.show = false;
          } else {
            this.show = true;
          }
          that.$nextTick(function() {
            that.addCss();
          });
        },
        response => {
          console.log("获取演员专题列表异常" + curl);
        }
      );
    },
    init() {
      let curl;
      if (this.$route.query.jsonUrl) {
        curl = this.$route.query.jsonUrl + this.behindParams;
        this.childrenUrl = this.$route.query.jsonUrl;
      } else {
        if (this.jsonUrl) {
          this.childrenUrl = this.jsonUrl;
          curl = this.jsonUrl + this.behindParams;
          localStorage.setItem("hotss", this.jsonUrl);
        } else {
          curl = localStorage.getItem("hotss") || "";
          this.childrenUrl = curl;
          curl += this.behindParams;
        }
      }
      this.getDetail(curl);
    },
    back() {
      if (this.$route.query.jsonUrl != undefined) {
        yh.back();
      } else {
        this.$router.go(-1);
      }
    },
    addCss() {
      $("#Img_" + this.num).addClass("changedImg");
    },
    removeCss() {
      $("#Img_" + this.num).removeClass("changedImg");
    },
    down() {
      if (this.op == 0) {
        $(".btn_sc").removeClass("change");
        $(".btn_js").removeClass("change");
        this.op++;
        this.addCss();
        this.js = false;
      }
    },
    up() {
      if (this.op == 1) {
        $(".btn_sc").addClass("change");
        this.op--;
        this.removeCss();
      }
    },
    left() {
      if (this.op == 0) {
        this.js = false;
        $(".btn_js").removeClass("change");
        $(".btn_sc").addClass("change");
      } else {
        if (this.num < 1) return;
        let left = this.cEleOffsetLeft("#Img_" + this.num);
        if (left < 100) {
          $(".special_list ul").css({
            transition: "all 0.1s",
            transform: "translateX(-" + 188 * (this.num - 1) + "px)"
          });
        }
        this.removeCss();
        this.num--;
        this.move();
        this.addCss();
      }
      //        this.textUrl = this.itemList[this.num].jsonUrl;
      //        this.inits(this.textUrl);
    },
    right() {
      if (this.op == 0) {
        // this.js = true;
        // $('.btn_sc').removeClass('change');
        // $('.btn_js').addClass('change');
      } else {
        let left = this.cEleOffsetLeft("#Img_" + this.num);
        if (this.num > this.itemList.length - 2) return;
        if (left > 900) {
          $(".special_list ul").css({
            transition: "all 0.1s",
            transform: "translateX(-" + 188 * (this.num - 4) + "px)"
          });
        }
        this.removeCss();
        this.num++;
        this.move();
        this.addCss();
      }
    },
    KeyEnter() {
      //op=0上方收藏
      if (this.op == 0) {
        if(this.show){
          this.Collect(1)
        }else{
          this.Collect(2)
        }
        if (!this.js) {
          //点击收藏
          if (this.show) {
            this.show = false;
            //H5本地缓存
            let Tvsc = {
              assetId: this.eleList.actorId,
              assetName: this.eleList.actorName,
              assetType: "明星",
              assetImg: this.eleList.coverimage,
              backgroundImg: this.eleList.backgroundImg,
              headImg: this.eleList.headImg,
              introduce: this.eleList.introduce,
              layout: "",
              layoutUrl: this.jsonUrl
            };
            let storage = localStorage.getItem("PersonMovies");
            if (storage) {
              storage = JSON.parse(storage);
            } else {
              storage = [];
            }
            storage.push(Tvsc);
            localStorage.setItem("PersonMovies", JSON.stringify(storage));
            this.Coll();
          } else {
            this.show = true;
            //H5删除收藏
            let storage = JSON.parse(localStorage.getItem("PersonMovies"));
            let json = [];
            for (let i = 0; i < storage.length; i++) {
              if (i != this.findindex()) {
                json.push(storage[i]);
              }
            }
            localStorage.setItem("PersonMovies", JSON.stringify(json));
            this.NoColl();
          }
        } else {
          // this.isShow = false;
          // this.alertMsg = {title: this.eleList.actorName, msg: this.eleList.introduce, alert: true,layout:false};
          // this.$refs.description.updateStast(true, "indown", true);
        }
      } else {
        //下方点击跳转
        window.localStorage.setItem('EnterType','5');
        let url = this.eleList.assetList[this.num].jsonUrl;
        let bp = {
          catCode: this.traceTypes,
          catId: this.tra,
          parent: 4,
          parentId: this.pageId
        };
        //          this.categoryCilck();
        localStorage.setItem("next", "返回首页");
        c.routerSkip(url, "1", bp, this.$router);
        this.pageLoad()
      }
    },
    //计算物体到左边的距离
    cEleOffsetLeft(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.left;
      return realHeight;
    },
    //收藏
    Coll() {
      try {
        let pageId = this.eleList.specialId;
        bi.collect(pageId, 0, 1);
      } catch (error) {
        console.log(error);
      }
    },
    NoColl() {
      // 取消收藏
      try {
        let pageId = this.eleList.specialId;
        bi.collect(pageId, 0, 2);
      } catch (error) {
        console.log(error);
      }
    },
    move() {
      let number = this.eleList.assetList.length;
      let ly = Math.ceil((1232 - 260) / number);
      console.log("移动距离" + ly);
      $(".btn_num").css({
        transition: "all 0.4s",
        transform: "translateX(" + ly * this.num + "px)"
      });
    },
    kDo(bl, name, pos) {
      if (pos == "KeyBack") {
        this.isShow = true;
      }
    }
  },
  computed: {
    ...mapGetters([
      "rootPath",
      "catList",
      "category",
      "behindParams",
      "isHTTPS"
    ])
  },
  beforeRouteLeave(to, from, next) {
    this.removeCss();
    this.op = 0;
    try {
      //          获取路径ID
      let traceId = c.getTrackPath();
      //          获取路径
      let traceType = c.getTrackPathType();
      let overTime = 0;
      overTime = new Date().getTime() - this.oldTime;
      bi.closPage(traceId, traceType, 4, this.pageId, overTime);
      console.log(traceId, traceType, 4, this.pageId, overTime);
      console.log("页面停留时间：" + overTime);
    } catch (error) {
      console.log(error);
    } finally {
      next();
    }
  },
  beforeRouteEnter (to, from, next) {
    window.sessionStorage.removeItem('srcPagePath');
    window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    next();
  },
  components: {
    description
  }
};
</script>



