<style>
.HistoryImg {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  box-sizing: border-box;
  border: none;
}
</style>
<template>
  <div class="special" :style="{backgroundImage:'url('+text.specialImg+')'}">
    <div class="special_box">
      <h2>{{msg}}</h2>
      <div class="right_ml">
        <div class="btn_sc change">
          <span v-if="!show">
            <!-- <i class="iconfont" v-if="!show">&#xe658    </i> -->
            <span class="jlt2" v-if="!show"></span>
          </span>
          <p v-if="!show" class="wz">已经</p><span class="jlt1" v-if="show"></span><p class="wz">收藏</p>
        </div>
        <div class="btn_ff" v-show="!isAuth">
          <div class="wz">
            <span class="jlt3"></span>
            <p>付费观看</p>
          </div>
        </div>
      </div>
      <div class="right_js">
        <p class="specialName">{{text.specialName}}</p>
        <div class="specialDesc">
          <h4>{{text.assetName}}</h4>
          <p>{{text.specialDesc}}</p>
        </div>
      </div>
    </div>
    <div class="special_list">
      <ul>
        <li v-for="(item,index) in text.elementList" :key="item.id" :id="('Img_'+index)">
          <lazy-image :src='item.elementImg' :placeholder='placeholder' :initScore='initScore' :score="item.score" :title="item.elementName"
            :imgClass='HistoryImg' />
        </li>
      </ul>
    </div>
    <div class="gunDong">
      <div class="btns " v-for="(item,index) in itemList" :key="item.id" :id="('btnss'+index)"></div>
    </div>
    <!-- 右下角提示框 -->
    <div class="prompt">
      <p>点击【方向下键】查看更多</p>
    </div>
    <div class="circle"></div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    <order v-if="isOrder" v-on:controllMeg="componentmMsg" :orders="orderList" :assetInfo="assetInfo" ></order>
  </div>
</template>
<style scoped="AllSpecial">
.btn_ff {
  float: left;
  width: 120px;
  height: 32px;
  border-radius: 15px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
  line-height: 32px;
  margin-top: 20px;
}

.gunDong {
  flex-direction: row-reverse;
  position: absolute;
  right: 29px;
  top: 202px;
}

.gunDong .btns {
  width: 18px;
  height: 18px;
  border: 1px solid white;
  border-radius: 50%;
  margin-bottom: 20px;
}

.gunDong .btns.changes {
  background: white;
}

.changedImg {
  border: 3px solid #ffffff;
  border-radius: 15px;
}

.right_ml .change {
  background: #0098bc;
  border: 2px solid white;
}

.special {
  width: 1280px;
  height: 720px;
  background-size: 100% 100%;
  border-top: 1px solid black;
  position: relative;
}

.special_box {
  margin-left: 75px;
  margin-top: 43px;
  margin-bottom:47px;
}

.special_box:after {
  clear: both;
  content: "";
  display: block;
}

.special .right_ml {
  float: left;
}

h2 {
  width: 1200px;
  color: white;
  font-size: 35px;
  line-height: 49px;
  margin-bottom: 0;
}

.btn_sc {
  float: left;
  box-sizing: border-box;
  width: 148px;
  height: 50px;
  border-radius: 16px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  margin-top: 28px;
  margin-right: 15px;
}

.btn_ff {
  float: left;
  box-sizing: border-box;
  width: 148px;
  height: 50px;
  border-radius: 16px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.2);
  margin-top: 28px;
}

.right_js {
  box-sizing: border-box;
  padding-left: 21px;
  float: right;
  margin-right: 110px;
  font-family: PingFangSC-Regular;
  color: #ffffff;
  width: 504px;
  height: 257px;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.75);
  font-size: 20px;
  /* overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical; */
}


.right_js h4{
  position: relative;
  margin-bottom:10px;
}
.specialDesc p {
  width: 442px;
  height: 190px;
  font-family: Microsoft YaHei;
  font-size: 20px;
  line-height: 31px;
  color: #ffffff;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;

}

.special_list {
  margin-left: 82px;
  width: 1120px;
  overflow: hidden;
}

.special ul {
  width: 100000%;
}

.special ul:after {
  clear: both;
  content: "";
  display: block;
}

.special ul li {
  width: 172px;
  height: 230px;
  float: left;
  margin-right: 16px;
  box-sizing: border-box;
}

.specialName {
  text-align: center;
  margin-top:10px;
  margin-bottom: 10px;
  font-weight: normal;
  font-size: 33px;
  line-height: 33px;
}

.jlt1 {
  width: 35px;
  height: 35px;
  margin-left: 34px;
  padding: 8px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -105px;
}

.jlt2 {
  width: 35px;
  height: 35px;
  margin-left: 15px;
  padding: 8px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -105px -105px;
}

.jlt3 {
  width: 35px;
  height: 35px;
  padding: 8px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -175px -175px;
}

p {
  font-size: 19px;
  display: inline-block;
  line-height: 50px;
}

.prompt {
  margin-top: 20px;
  margin-left: 795px;
  width: 300px;
  border-radius: 15px;
  text-align: center;
  font-size: 22px;
  letter-spacing: 1.4px;
  line-height: 57px;
  color: #1f436e;
  background-color: #fff;
}

.circle {
  margin-left: 815px;
  margin-top: 5px;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background-color: #fff;
}
</style>

<script>
import { mapGetters } from "vuex";
import { HTTPUtil as api } from "../../fetch/api.js";
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import { Authentication as auth } from "../../js/auth.js";
import order from "../../tool/order/order.vue";
import Toast from "../../tool/toast/toast.js";
import { xha as x } from "../../js/xha.js";
export default {
  props: ["jsonUrl", "bp"],
  name: "AllSpecial",
  data() {
    return {
      show: true,
      op: 0,
      ops: true,
      isShow: true,
      num: 0,
      item: 18,
      HistoryImg: "HistoryImg",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      //        jsonUrl: "http://183.131.15.28/?s=19|7&p=yhSpecialDetail&k=1&v=2&specialId=202688&c=7",
      //        jsonUrl: "http://183.131.15.28/?s=19|7&p=yhSpecialDetail&k=1&v=2&specialId=202737&c=7",
      msg: "",
      isOnceReq: true,
      text: {description:""},
      eleList: {},
      itemList: [],
      textUrl: "",
      opTime: 0,
      tra: "",
      pageId: "",
      traceTypes: "",
      traceType: "",
      pageType: 8,
      specialType: 1,
      isOrder: false,
      orderList: null, // 可订购列表
      assetInfo: null,
      isAuth: true, //  鉴权标识 2018.10.26 屏蔽收费入口
      authStartTime: 0, // 鉴权开始时间
      authEndTime: 0, // 鉴权结束时间
      columnId: 0,
      pid: 0, // 可订购产品包ID
      sid: 0, // 可订购产品包服务ID
      sname: "", // 可订购产品名称
      detailData: null,
      authCode:0
    };
  },
  components: {
    order,
    Toast
  },
  created() {
    window.topicAuth = this.topicAuth;
    window.topicToken = this.topicToken;
    this.init();
    this.$nextTick(function(){
      setTimeout(() => {
        $("#btnss" + this.opTime).addClass("changes");
      }, 100);
    })
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  methods: {
    getAssentsPPV() {
      // CMS 查询绑定关系
      this.pageId = this.text.elementList[0].elementId;
      let assetName = this.text.elementList[0].elementName;
      let spCode = this.text.elementList[0].providerId;
      this.assetInfo = { contentCode: this.pageId, contentName: assetName,assetSp:spCode};
      api.getProducts(this.pageId, this.columnId).then(jsonData => {
          console.log(JSON.stringify(jsonData));
          this.isOnceReq = false; // 有鉴权结果就不需要再鉴权了
          if (jsonData.code == 200) {
            if (jsonData.data.result.length > 0) {
              console.log("收费资产：资产ID:" + this.pageId);
              const pob = jsonData.data.result[0].pgList[0];
              console.log(pob);
              this.pid = pob.productCode;
              this.sid = pob.serviceCode;
              this.sname = pob.serviceName;
              console.log("pob.productCode=======" + pob.productCode);
              // let params = {};
              //     params.url = "/api/Auth";
              //     params.type = "post";
              //     params.data = JSON.parse(auth.AuthParams(this.pid,this.sid,this.pageId));
              //     api.ajax(params).then((jsonData)=>{
              //         console.log("BMS返回:"+JSON.stringify(jsonData));
              //         this.topicAuth(jsonData);
              // })
              this.authStartTime = new Date().getTime();
              yh.HttpPost(
                auth.Auth,
                auth.AuthParams(this.pid, this.sid, this.pageId),
                "topicAuth"
              ); // 产品包鉴权请求
            } else {
              console.log("免费资产直接播");
              this.isAuth = true;
            }
          }
        })
        .catch(() => {
          Toast({ message: "CMS查询产品包信息异常", position: "bottom" });
          // this.isAuth = false;
          this.isAuth = true; // 2018.10.26屏蔽专题收费按钮
        });
    },
    topicAuth(jsonData) {
      // BMS 专题
      this.authEndTime = new Date().getTime() - this.authStartTime;
      console.log("BMS响应时间:" + this.authEndTime);
      console.log("请求BMS 鉴权返回:" + JSON.stringify(jsonData));
      if (jsonData.code == 200) {
        if (jsonData.result) {
          // 存在可订购列表
          this.authCode = jsonData.result.resultCode;
          if (jsonData.result.resultCode == "0000") {
            console.log("已订购");
            this.isAuth = true;
            // yh.updatePreViwe(false); // 鉴权成功通知播放器无需试看
          }else if(jsonData.result.resultCode == "1001" ||jsonData.result.resultCode=="1002" || jsonData.result.resultCode=="1005"){
            this.orderList = null;
            // this.isAuth = false;
            this.isAuth = true; // 2018.10.26屏蔽专题收费按钮
            console.log("用户不存在或非法,请退出重试。");
          }else{
            this.orderList = jsonData.result.products;
            // this.isAuth = false;
            this.isAuth = true; // 2018.10.26屏蔽专题收费按钮
            console.log("未订购");
          }
        }
      } else if (jsonData.code === 1004 || "1004" === jsonData.code) {
        // 用户token失效，续租
        console.log("token令牌失效重新续租");
        // this.isAuth = false;
        this.isAuth = true; // 2018.10.26屏蔽专题收费按钮
        this.restUserToken();
      } else {
        this.isAuth = true; // 鉴权失败
        Toast({ message: "用户鉴权失败,请退出重试", position: "bottom" });
        yh.updatePreViwe(false); // 鉴权超时通知播放器无需试看
      }
    },
    restUserToken() {
      // 续租token
      yh.HttpPost(auth.Login, auth.LoginParams(), "topicToken"); // 终端请求
    },
    topicToken() {
      // BMS 续租结果处理
      if (jsonData.code == 200) {
        if (jsonData.result.resultCode == "0000") {
          try {
            yh.setAuthInfo(jsonData.result);
            localStorage.setItem("AAA", "true");
            localStorage.setItem("AuthResult", JSON.stringify(jsonData.result));
            this.$store.commit("GET_AUTH", "true");
            yh.HttpPost(
              auth.Login,
              auth.AuthParams(this.pid, this.sid, this.pageId),
              "topicAuth"
            ); // 续租成功继续鉴权
          } catch (error) {
            console.log("鉴权成功，处理参数转换异常");
          }
        } else {
          localStorage.setItem("AAA", "false");
          this.$store.commit("GET_AUTH", "false");
          Toast("续租token失败,用户账号异常");
        }
      } else {
        Toast("续租token失败,请退出详情页重试");
      }
    },
    componentmMsg(bl, name, pos, url) {
      // 处理支付组件之间的消息
      console.log(bl, name, pos, url);
      if (name == "order" || name == "orderBack") {
        if (pos == "orderCancle") {
          this.isOrder = false;
          this.isShow = true;
          if (url === "true" || url === true) {
            Toast("支付成功~");
          } else {
            Toast("支付失败~");
          }
        } else if (pos == "orderBack") {
          this.isOrder = false;
          this.isShow = true;
        }
      }
    },
    keyCode(kc) {
      console.warn(kc)
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        this.KeyEnter();
      }
    },
    //      categoryCilck() {
    //        let pageId = this.eleList.specialId;
    //        let traceId = "|" + this.bp.catId + "|" + pageId;
    //        let traceType = "|" + this.bp.catCode + "|8|";
    //        //      bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
    //        bi.categoryClick(this.bp.catId, this.catid, 3, 8, pageId, traceId, traceType);
    //        console.log(this.bp.catId, this.catid, 3, 8, pageId, traceId, traceType);
    //      },
    // 这里是埋点工作
    load() {
      $("#btnss" + this.opTime).addClass("changes");
      try {
        let pageId = this.eleList.specialId;
        c.setTrackPath(pageId, this.pageType);
        this.itTime = new Date().getTime();

        //          获取路径ID
        let traceId = c.getTrackPath();
        //          获取路径
        let traceType = c.getTrackPathType();

        //          获取父Id
        let catId = c.getParentPageId();
        //          获取父类型
        let catCode = c.getParentPageType();
        //
        let oldTime =
          performance.timing.domContentLoadedEventEnd -
          performance.timing.navigationStart;
        //        bp.loadPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,special_type,oldTime)
        bi.loadPage(
          traceId,
          traceType,
          this.pageType,
          pageId,
          catCode,
          catId,
          oldTime
        );
        let PagePath = c.getTrackPathType().replace(/\|/gi, "_");
        let SrcPage = PagePath.split("_");
        let SrcPagePath = SrcPage.slice(0, SrcPage.length - 1).join("_");
      } catch (error) {
        console.log(error);
      }
    },
    closPages() {
      // 离开页面（页面停留时间）
      let time = new Date().valueOf();
      let times = time - this.oldTimes;
      this.pageId = this.eleList.specialId;
      // 获取路径ID
      let traceId = c.getTrackPath();
      // 获取路径
      let traceType = c.getTrackPathType();
      //获取special_type
      let special_type = this.specialType;
      bi.closPage(
        traceId,
        traceType,
        this.pageType,
        31,
        this.pageId,
        special_type,
        times
      );
    },
    findindex() {
      let storage = JSON.parse(localStorage.getItem("PersonInfo"));
      for (let i = 0; i < storage.length; i++) {
        if (storage[i].specialId == this.eleList.specialId) {
          return i;
        }
      }
    },
    //B查看本地缓存是否存在
    queryFavorites() {
      let storage = JSON.parse(localStorage.getItem("PersonInfo"));
      if (storage) {
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].specialId == this.eleList.specialId) {
            return true;
          }
        }
      }
    },

    inits(Url) {
      let curl;
      if (Url) {
        this.childrenUrl = Url;
        curl = Url + this.behindParams;
      } else {
        this.childrenUrl = curl;
        curl += this.behindParams;
      }
      this.getDetails(curl);
    },
    getDetails(curl) {
      // curl = "http://183.131.15.11:8490/?s=11|1&p=yhSpecialDetail&k=1&v=1&specialId=202870&c=1&returnType=jsonp";
      //        小专题
      let that = this;
      api.get(curl).then(
        data => {
          let catList = data.data;
          console.log("获取专题数据成功");
          that.text = catList;
          if (this.isOnceReq) {
            this.getAssentsPPV(); // 专题鉴权
          }
        },
        response => {
          console.log("获取详情页数据失败");
        }
      );
    },
    init() {
      let curl;
      if (this.$route.query.jsonUrl) {
        curl = this.$route.query.jsonUrl + this.behindParams;
        this.childrenUrl = this.$route.query.jsonUrl;
      } else {
        if (this.jsonUrl) {
          this.childrenUrl = this.jsonUrl;
          curl = this.jsonUrl + this.behindParams;
          localStorage.setItem("hot", this.jsonUrl);
        } else {
          curl = localStorage.getItem("hot") || "";
          this.childrenUrl = curl;
          curl += this.behindParams;
        }
      }
      this.getDetail(curl);
    },
    getDetail(curl) {
      let that = this;
      api.get(curl).then(data => {
          let catList = data.data;
          that.eleList = catList;
          that.itemList = that.eleList.elementList;
          console.warn(that.itemList)
          that.msg = this.eleList.specialName;
          if (this.queryFavorites()) {
            this.show = false;
          } else {
            this.show = true;
          }
        },
        response => {
          console.log("获取详情页数据失败");
        }
      );
    },
    back() {
      console.log('返回')
        this.$router.go(-1);
    },
    addCss() {
      $("#Img_" + this.num).addClass("changedImg");
    },
    removeCss() {
      $("#Img_" + this.num).removeClass("changedImg");
    },
    down() {
      if (this.op == 0) {
        $(".btn_sc").removeClass("change");
        $(".btn_ff").removeClass("change");
        this.ops = true;
        this.op++;
        this.num = 0;
        this.addCss();
      } else {
        if(this.itemList.length-this.opTime <= 1)return;
        if (this.opTime > this.text.elementList.length - this.opTime) return;
        if ($("#btnss1").hasClass("changes")) {
          $(".prompt").css("display", "none");
          $(".circle").css("display", "none");
        }
        $("#btnss" + this.opTime).removeClass("changes");
        this.opTime++;
        this.textUrl = this.itemList[this.opTime].jsonUrl;
        this.inits(this.textUrl);
        this.removeCss();
        this.op = 0;
        this.num = 0;
        $(".btn_sc").addClass("change");
        $("#btnss" + this.opTime).addClass("changes");
        $(".special_list ul").css({transition: "all 0.1s",transform: "translateX(0px)"});
      }
    },
    up() {
      if (this.op == 1) {
        $(".btn_sc").addClass("change");
        this.op--;
        this.removeCss();
      } else {
        if (this.opTime <= 0) return;
        if ($("#btnss2").hasClass("changes")) {
          $(".prompt").css("display", "block");
          $(".circle").css("display", "block");
        }
        $("#btnss" + this.opTime).removeClass("changes");
        this.opTime--;
        this.textUrl = this.itemList[this.opTime].jsonUrl;
        this.inits(this.textUrl);
        this.op = 1;
        this.num = 0;
        this.addCss();
        $(".btn_sc").removeClass("change");
        $(".btn_ff").removeClass("change");
        $("#btnss" + this.opTime).addClass("changes");
        $(".special_list ul").css({
          transition: "all 0.1s",
          transform: "translateX(0px)"
        });
      }
    },
    left() {
      if (this.op == 0) {
        $(".btn_ff").removeClass("change");
        $(".btn_sc").addClass("change");
        this.ops = true;
      } else {
        if (this.num < 1) return;
        let left = this.cEleOffsetLeft("#Img_" + this.num);
        if (left < 100) { // 解决翻页滚动到第二个不能继续滚动
          $(".special_list ul").css({
            transition: "all 0.1s",
            transform: "translateX(-" + 188 * (this.num - 1) + "px)"
          });
        }
        this.removeCss();
        this.num--;
        this.move();
        this.addCss();
      }
    },
    right() {
      if (this.op == 0) {
        if (!this.isAuth) {
          $(".btn_sc").removeClass("change");
          $(".btn_ff").addClass("change");
          this.ops = false;
        }
      } else {
        let left = this.cEleOffsetLeft("#Img_" + this.num);
        if (this.num > this.text.elementList.length - 2) return;
        if (left > 900) {
          $(".special_list ul").css({
            transition: "all 0.1s",
            transform: "translateX(-" + 188 * (this.num - 4) + "px)"
          });
        }
        this.removeCss();
        this.num++;
        this.move();
        this.addCss();
      }
    },
    pageLode(asssetId,asssetName) {
      let SrcPagePath = this.category[this.navpos].catName;
      let PageName = asssetName;
      let AsssetID = asssetId;
      let Id = localStorage.getItem("position").split('_')[0];
      let TYPE = "";
      x.pageLoad("专题",SrcPagePath, PageName, AsssetID, Id, TYPE);
    },
    Collect(Status) {
      // 星红桉埋点
      let cId = this.eleList.specialId; // 媒资ID
      let cName = this.eleList.specialName; // 媒资名字
      let cStatus = Status; // 媒资收藏状态
      x.collect(cId, cName, cStatus);
    },
    KeyEnter() {
      //op=0上方收藏
      if (this.op == 0) {
        if (this.ops) {
          if (this.show) {
            this.show = false;
            this.Collect(1);
            //H5本地缓存
            let Tvsc = {
              specialId: this.eleList.specialId,
              assetName: this.eleList.specialName,
              assetType: "大专题",
              assetImg: this.eleList.coverimage,
              episodes: "",
              broadcast: "",
              score: "",
              coverimage: this.eleList.coverimage,
              specialName: this.eleList.specialName,
              layout: "",
              layoutUrl: this.jsonUrl
            };
            let storage = localStorage.getItem("PersonInfo");
            if (storage) {
              storage = JSON.parse(storage);
            } else {
              storage = [];
            }
            storage.push(Tvsc);
            localStorage.setItem("PersonInfo", JSON.stringify(storage));
            this.Coll();
          } else {
            this.Collect(2);
            this.show = true;
            //H5删除收藏
            let storage = JSON.parse(localStorage.getItem("PersonInfo"));
            let json = [];
            for (let i = 0; i < storage.length; i++) {
              if (i != this.findindex()) {
                json.push(storage[i]);
              }
            }
            localStorage.setItem("PersonInfo", JSON.stringify(json));
            this.NoColl();
          }
        } else {
          if (!this.isAuth) {
            if (this.orderList == null) {
              if(this.authCode==0){
                Toast({ message: "正在查询中请稍后~", position: "bottom" });
              }else if(this.authCode=="1001"||this.authCode=="1002"){
                Toast({ message: "账户不存在或非法，请联系客服。", position: "bottom" });
              }else{
                Toast({ message: "鉴权异常，请退出重试。", position: "bottom" });
              }
            } else {
              this.isShow = false;
              this.isOrder = true;
            }
          } else {
            Toast({ message: "您已经订购过啦~", position: "bottom" });
          }
        }
      } else {
        //下方点击跳转
        let url = this.text.elementList[this.num].jsonUrl;
        this.catid = this.text.elementList[this.num].elementId;
        //          this.categoryCilck();
        let obj = {
          catCode: this.traceTypes,
          catId: this.catid,
          parent: 8,
          parentId: this.eleList.specialId,
          special_type: 1,
          special_id: this.eleList.specialId
        };
        this.pageLode(this.text.assetId,this.text.assetName);
        console.log(obj);
        c.routerSkip(url, "1", obj, this.$router);
        window.localStorage.setItem('EnterType','2');
      }
    },
    //计算物体到左边的距离
    cEleOffsetLeft(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.left;
      return realHeight;
    },
    //收藏
    Coll() {
      try {
        let pageId = this.eleList.specialId;
        bi.collect(pageId, 0, 1);
      } catch (error) {
        console.log(error);
      }
    },
    //取消收藏
    NoColl() {
      try {
        let pageId = this.eleList.specialId;
        bi.collect(pageId, 0, 2);
      } catch (error) {
        console.log(error);
      }
    },
    move() {
      let number = this.eleList.elementList.length;
      let ly = Math.ceil(1133 / number - 10);
      $(".btn_num").css({
        transition: "all 0.4s",
        transform: "translateX(" + ly * this.num + "px)"
      });
    }
  },
  computed: {
    ...mapGetters([
      "rootPath",
      "catList",
      "category",
      "behindParams",
      "isHTTPS",
      "httpRequestTime",
      "httpResponseTime",
      "navpos"
    ])
  },
  beforeRouteLeave(to, from, next) {
    this.removeCss();
    this.op = 0;
    try {
      let traceId = c.getTrackPath();
      let traceType = c.getTrackPathType();
      let overTime = 0;
      overTime = new Date().getTime() - this.itTime;
      bi.closPage(traceId, traceType, 8, this.pageId, 1, overTime);
    } catch (error) {
      console.log(error);
    } finally {
      next();
    }
  },
  beforeRouteEnter (to, from, next) {
    window.sessionStorage.removeItem('srcPagePath');
    window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    next();
  },
  watch: {
    eleList() {
      // 监听数据变化并作出处理
      this.$nextTick(function() {
        this.isShow = true;
        this.textUrl = this.itemList[this.opTime].jsonUrl;
        this.inits(this.textUrl);
        this.load();
      });
    },text(){
      
        let jianjie = this.text.specialDesc;
        let reslut =  this.text.specialDesc;
        if(jianjie.indexOf('简介')>=0){
          reslut =  jianjie.substring(3);
        }else if(jianjie.indexOf('简介:')>=0){
          reslut = jianjie.substring(3);
        }else if(jianjie.indexOf('简介：')>=0){
          reslut = jianjie.substring(3);
        }
        this.text.specialDesc = '简介：'+reslut;
    }
  }
};
</script>



